<script>
    module.exports = {
        route: {

        },
        ready(){

        },
        data() {
            return {
                userData: pageMess.userData,
                options: [
                  { text: '女', value: 1 },
                  { text: '男', value: 2 },
                  { text: '未知', value: 3 }
                ]
            }
        },
        computed: {
            strImageUrl () {
                return this.userData.strImageUrl || "https://st.midea.com/pc/img/my/user/user_default_portrait_120x120.png"
            },
            strNickname () {
                return this.userData.strNickname || '未设置'
            },
            sex (){
                return this.userData.sGender === 0 ? 3:this.userData.sGender
            },
            birthday (){
                return
            }
        },
        methods: {
            editNickName () {
                router.go({ name: 'editnickname'})
            }
        }
    }
</script>

<template>
<div>
    <header>
        <div class="md_hd_wrap" id="headContent"><div class="col_left "><a href="javascript:void(0);"><i id="backChildBtn" class="hd_back" attr-tag="backBtn"></i></a></div><div class="col_middle"><h1 id="title">个人信息</h1></div></div>
    </header>
    <div class="main_wrap_inner">
        <div class="mod_list">
            <div class="mod_list_item" data-mtag="30045.1.8">
                <div class="list_item_bd">
                    <div class="list_item_label"></div>
                    <div class="list_item_cnt">
                        头像
                    </div>
                    <div class="list_item_extra ">
                        <div class="photo">
                            <img class="user_portrait" width="46" height="46" src="{{strImageUrl}}">
                        </div>
                    </div>
                </div>
            </div>
            <div class="mod_list_item" data-mtag="30045.1.1">
                <div class="list_item_bd" id="J_nickname">
                    <div class="list_item_label"></div>
                    <div class="list_item_cnt">
                        昵称
                    </div>
                    <div class="list_item_extra">
                        <a v-link="{path: 'editnickname'}">
                            <span id="txtNickname">{{strNickname}}</span>
                            <i class="mod_arrow_r"></i>
                        </a>
                    </div>
                </div>
            </div>
            <div class="mod_list_item" data-mtag="30045.1.4">
                <div class="list_item_bd" id="J_gender">
                    <div class="list_item_label"></div>
                    <div class="list_item_cnt">
                        性别
                    </div>
                    <div class="list_item_extra">
                        <select v-model="sex">
                          <option v-for="option in options" v-bind:value="option.value">
                            {{ option.text }}
                          </option>
                        </select>
                        <i class="mod_arrow_r"></i>
                    </div>
                </div>
            </div>
            <div class="mod_list_item" data-mtag="30045.1.5">
                <div class="list_item_bd" id="J_birth">
                    <div class="list_item_label"></div>
                    <div class="list_item_cnt">
                        生日
                    </div>
                    <div class="list_item_extra"><input id="txtBirth" type="text" class="date" v-model="userData.lBirthday" placeholder="请填写生日" /><i class="mod_arrow_r"></i></div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<style lang="sass">

</style>


