import App from './App' // 主框架
import UserInfoView from './pages/UserInfoView'// 首页
import EditNickName from './pages/EditNickName'// 修改昵称

Vue.use(VueRouter)

var router = new VueRouter()

router.map({
    '/': {
        component: UserInfoView
    },
    'editnickname': {
        component: EditNickName
    }
})

router.beforeEach(function () {
    window.scrollTo(0, 0)
})

router.redirect({
    '*': '/'
})

router.start(App, '#app')



